<template>
    <div class="all_com">
        <div class="title_com">
            <span>线下订单管理</span>
            <el-select
                    v-model="value1"
                    placeholder="请选择"
                    @change="selectStair">
                <el-option
                        v-for="item in options1"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>
        </div>
        <!-- <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>批量发货</span>
            </div>
        </el-card> -->
        <orderFilter>

        </orderFilter>
        <orderComponent
                :orderList = "orderList"
                :totalNo = "totalNo"
                :isOffine = "isOffine">
        </orderComponent>
    </div>
</template>
<script>
    import orderComponent from '../../components/orderComponent.vue'
    import orderFilter from '../../components/orderFilter.vue'
    export default {
        data () {
            return {
                options1: [{
                    value: 'ALL_ORDER',
                    label: '订单列表'
                }, {
                    value: 'UNPAID',
                    label: '待确认支付'
                }, {
                    value: 'PRESELL_PAID',
                    label: '已支付订金'
                }, {
                    value: 'BEGIN_FINAL_PAY',
                    label: '开始支付尾款'
                }, {
                    value: 'WAIT_FOR_FINAL_PAY',
                    label: '待支付尾款'
                }, {
                    value: 'PAID',
                    label: '已完成支付'
                }, {
                    value: 'UNDELIVERED1',
                    label: '待发货（发货）'
                },{
                    value: 'UNDELIVERED2',
                    label: '待发货（托管）'
                }, {
                    value: 'UNDELIVERED3',
                    label: '已安排发货'
                }, {
                    value: 'DELIVERED1',
                    label: '已发货（发货）'
                }, {
                    value: 'DELIVERED2',
                    label: '已发货（托管）'
                }, {
                    value: 'COMPLETED',
                    label: '已确认收货'
                }, {
                    value: 'CANCELED',
                    label: '已取消'
                },{
                    value: 'EXPIRED',
                    label: '已过期'
                }],
                value1: 'ALL_ORDER',
                orderList: [],
                totalNo: 0,
                isOffine: true, // 是否为线下订单参数
                filterMsg: {},
                searchMsg: {}
            }
        },
        components: {
            orderComponent,
            orderFilter
        },
        methods: {
            selectStair(){
                this.getOfflineOder(this.value1)
                this.msg.$emit('value1',this.value1);
            },
            getOfflineOder(status){
                // 若this.value1为空，后端不判断addressId
                if(status == 'UNDELIVERED1'){
                    status = 'UNDELIVERED'
                    // IsHostingOrder传0，为待发货（发货）
                    this.IsHostingOrder = 0
                }else if(status == 'UNDELIVERED2'){
                    status = 'UNDELIVERED'
                    // IsHostingOrder传1，为待发货（托管）
                    this.IsHostingOrder = 1
                }else if(status == 'UNDELIVERED3'){
                    status = 'UNDELIVERED'
                    // ReadyDeliver=1为已安排发货，0为待发货
                    this.ReadyDeliver = 1
                }else if(status == 'DELIVERED1'){
                    // 已发货需判断是否托管
                    status = 'DELIVERED'
                    this.IsHostingOrder = 0
                }else if(status == 'DELIVERED2'){
                    // 已发货需判断是否托管
                    status = 'DELIVERED'
                    this.IsHostingOrder = 1
                }
                this.$api('/admin/offline/get_offline_order_list', {
                    productName: this.filterMsg.value2,
                    payment: this.filterMsg.value3,
                    lang: this.filterMsg.value4,
                    createStartTime: this.filterMsg.createStartTime,
                    createEndTime: this.filterMsg.createEndTime,
                    updateStartTime: this.filterMsg.updateStartTime,
                    updateEndTime: this.filterMsg.updateEndTime,
                    readyDeliver: this.ReadyDeliver,
                    isHostingOrder: this.IsHostingOrder,
                    status: status == 'ALL_ORDER'? '':status,
                    orderNo: this.searchMsg.orderNo,
                    account: this.searchMsg.account,
                    email: this.searchMsg.email,
                    expressOrderNumber: this.searchMsg.expressOrderNumber,
                    receiverName: this.searchMsg.receiverName,
                    hostName: this.searchMsg.hostName,
                    pageNo:1,
                    pageSize:50
                }).then(res => {
                    let d = res.data
                    console.log(d)
                this.orderList = d.orderList
                this.totalNo = d.page.totalNum
            });
            }
        },
        watch: {
            filterMsg: { // 深度监听过滤条件变化，并刷新页面
                handler(val, oldVal){
                    // console.log(val, oldVal)
                    this.getOfflineOder(this.value1)
                },
                deep:true
            },
            searchMsg: { // 深度监听搜索条件变化，并刷新页面
                handler(val, oldVal){
                    console.log(val, oldVal)
                    this.getOfflineOder(this.value1)
                },
                deep:true
            },
        },
        mounted(){
            // 拿到过滤条件
            this.msg.$on('filterBtn',(res)=>{
                console.log(res)
            this.filterMsg = res
        })
            // 拿到搜索条件
            this.msg.$on('searchBtn',(res1)=>{
                // console.log(res1)
                this.searchMsg = res1
        })
        },
        created(){
            this.getOfflineOder()
        }
    }
</script>
<style lang='scss' scoped>
    @import "../../assets/css/titlecom.scss";

</style>


